body{
  box-sizing: content-box;
  font-family: montserrat;
  margin: 0;
}

.cardsContainer{
  color: black;
  text-align: center;
  justify-content: center;
}

@media (min-width: 1200px)
{
  .container, .container-sm, .container-md, .container-lg, .container-xl {
    max-width: 90%;
}
}

.mainHeader{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Montserrat, sans-serif;
    color: aliceblue;
    margin-bottom: 50px;
    margin-top: 10%;
}
.searchHeader{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Montserrat, sans-serif;
    color: aliceblue;
    margin-bottom: 25px;
    margin-top: 2%;
}
.failSearchMessage{
    font-family: Montserrat, sans-serif;
    color: aliceblue;
    margin-bottom: 25px;
    margin-top: 5%;
}

.backButton{
    width: 20%;
    height: 70px;
    background-color: transparent;
    color: aliceblue;
    font-family: Montserrat;
    font-size: 27px;
    border-color: aliceblue;
}
.backButton:focus{
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
}
.backButton:hover{
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
    transition: .4s;
}

@media(max-width: 767px){
    .mainHeader h1{
        font-size: 35px;
    }
    .mainHeader h2{
        font-size: 30px;
    }
    .backButton{
        width:50%;
    }
}


@media(max-width: 500px){
    .mainHeader h1{
        font-size: 32px;
    }
    .mainHeader h2{
        font-size: 28px;
    }
    .backButton{
        width:80%;
    }
}
.heroImg{
    position: fixed;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    background-image: url(/planet-searcher/static/media/heroimg.2b5803c3.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 80% 0%;
}
.searchInput{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 3%;
}
.searchInput input{
    width: 32%;
    font-size: 30px;
    font-family: Montserrat;
    color: aliceblue;
    background-color: transparent;
    border-color: unset;
    border: 0;
    border-bottom: 1px solid aliceblue;
    background: none;
    margin-bottom: 3%;
    text-align: center;
    transition: box-shadow .3s ease-out;
}
.searchInput input:focus{
    outline: none;
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
}
.searchInput input:hover{
    outline: none;
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);

}
.searchInput label{
    font-family: Montserrat, sans-serif;
}

.infoBox{
margin: 2% 0;
color: aliceblue;
border: 1px solid aliceblue;
font-size: 18px;
font-family: Montserrat;
padding: 1%;
transition: box-shadow .3s ease-out;
}
.infoBox:hover{
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
}
.infoInBox{
    width: 40%;
    font-family: Montserrat;
    color: aliceblue;
    font-size: 18px;
   padding: 1%;
    transition: box-shadow .3s ease-out;
    border: 1px solid aliceblue;
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
}

.infoInBox span{

}
.searchButton{
    width: 20%;
    height: 70px;
    background-color: transparent;
    color: aliceblue;
    font-family: Montserrat;
    font-size: 27px;
    border-color: aliceblue;
}
.searchButton:focus{
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
}
.searchButton:hover{
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
    transition: .4s;
}

@media(max-width: 767px){
    .searchInput input{
        width: 50%;
    }

    .searchButton{
    width: 40%;
    }
}

@media(max-width: 500px){
    .searchInput input{
        margin: 10% 0;
        width: 90%;
    }

    .searchButton, .infoBox, .infoInBox{
    width: 80%;
    }
 
}
.planetBox{
    background: aliceblue;
    margin: 4%;
    transition: .4s;
}
.planetBox:hover{
    background: #d7ecff;
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
    transition: .4s;
}
.backgroundImgStyle{

    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    transition: .4s;
}

.titleStyle{
    padding: 4%;
    font-size: 16px;
    font-weight: 500;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: .8s;
}

@media(max-width:767px){
    .titleStyle{
        white-space: break-spaces;
    }
}
.infoWindow{
background-color: white;
position: absolute;
display: block;
width:70%;
height: 15vh;
top: 25vh;
z-index: 10;
margin: 0 15%;
}

.showModal{
    font-family: 'Montserrat', sans-serif;
}

.showModal h4{
    margin-bottom: 3%;
    text-align: center;
}

.showModal img{
    width: 100%;
    background-size: cover;
    background-position: 80% 0%;
}

.textInModal p{
    text-align: left;
    text-overflow: ellipsis;
    word-break: break-word;
    padding: 1%;
}

.closeBtn{
    background: transparent !important;
    color: black !important;
    border-color: black !important;
    width: 15% !important;
    transition: 0.4s !important;
}
.closeBtn:hover{
    background-color: #d7ecff !important;
    border-color: #bbb !important;
    transition: 0.4s;
}

@media(max-width: 500px){
    .closeBtn{
        width: 80% !important;
        margin: 0 10% !important;
    }
}
