.planetBox{
    background: aliceblue;
    margin: 4%;
    transition: .4s;
}
.planetBox:hover{
    background: #d7ecff;
    box-shadow: 0 10px 20px -8px rgba(255,255,255, .5);
    transition: .4s;
}
.backgroundImgStyle{

    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    transition: .4s;
}

.titleStyle{
    padding: 4%;
    font-size: 16px;
    font-weight: 500;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: .8s;
}

@media(max-width:767px){
    .titleStyle{
        white-space: break-spaces;
    }
}